/**
Global styles
*/
body{
    /* background: rgba(82, 210, 241, 0.3); */
    background: linear-gradient(to right top, #65dfc9, #6cdbeb);
    font-family: $global-font;
    min-height: 100vh;
  }
  
  .page-size{
    display: flex;
    min-height: 70vh;
    background: linear-gradient(to right bottom,$page-color1,$page-color2);
    border-radius: 20px;
    padding: 20px;
    margin-bottom:15px;
    /* box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19); */
  }
  
  .container {
    max-width: 100%;
    height: 100%;
  }
  
  .primary-text {
    font-size: $primary-font-size;
    font-weight: bold;
    color: black;
  }
  
  
  .secondary-text {
    font-size: $secondar-font-size;
    font-weight: normal;
    color: black;
    font-style: italic;
  }
  
  .m-left {
    margin: 7px;  
  }
  
  .page-margin {
    padding: 10px 15px;
  }
  